$icons: (0:0)
$icons: map-merge($icons,(clock: (X: 0px, Y:0px, W: 31px, H: 31px, TW: 66px, TH: 49px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(codedby: (X: 0px, Y:-44px, W: 47px, H: 5px, TW: 66px, TH: 49px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(coderiver: (X: 0px, Y:-35px, W: 49px, H: 5px, TW: 66px, TH: 49px, IMG: '../img/icons.png')))
$icons: map-merge($icons,(world: (X: -35px, Y:0px, W: 31px, H: 31px, TW: 66px, TH: 49px, IMG: '../img/icons.png')))


// Gets an attribute from the sass map
@function icon-attr($icon, $attr)
	$icon: map-get($icons, $icon)
	@return map-get($icon, $attr)

@mixin sprite($iconName)
	background-image: url(icon-attr($iconName, IMG))
	width: icon-attr($iconName, W)
	height: icon-attr($iconName, H)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-position($iconName)
	background-position: icon-attr($iconName, X) icon-attr($iconName, Y)
@mixin sprite-retina($iconName)
	background-image: url(icon-attr($iconName, IMG))
	$width: icon-attr($iconName, W)
	$height: icon-attr($iconName, H)
	width: $width/2
	height: $height/2
	$x: icon-attr($iconName, X)
	$y: icon-attr($iconName, Y)
	background-position: $x/2 $y/2
	$tw: icon-attr($iconName, TW) 	
	$th: icon-attr($iconName, TH)
	background-size: $tw/2 $th/2  	


@mixin s($i)
	+sprite($i)
@mixin sp($i)
	+sprite-position($i)
@mixin sr($i)
	+sprite-retina($i)	

.icon-clock
	width: 31px
	height: 31px
	background-image: url('../img/icons.png')
	background-position: 0px 0px
.icon-codedby
	width: 47px
	height: 5px
	background-image: url('../img/icons.png')
	background-position: 0px -44px
.icon-coderiver
	width: 49px
	height: 5px
	background-image: url('../img/icons.png')
	background-position: 0px -35px
.icon-world
	width: 31px
	height: 31px
	background-image: url('../img/icons.png')
	background-position: -35px 0px
